                this.scene = this.story.scenes.find(scene => scene.name === planItem.scene);
            }
            if (planItem.character) {
                this.character = this.story.characters.find(character => character.name === planItem.character);
            }
            if (planItem.text) {
                this.text = planItem.text;
            }
            this.loadMetadataAndDraw();
        },
        nextStep() {
            this.index++;
            if (this.index >= this.story.plan.length) {
                this.$router.push("/");
            } else {
                this.update();
            }
