
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

import { v4 as uuidv4 } from 'uuid';
import myLocalStorage from "@/myLocalStorage.js";

export default new Vuex.Store({
    state: {
        story: {
            scenes: [],
            characters: [],
            plan: []
        },
        stories: myLocalStorage.loadStories()
    },
    getters: {
        story(state) {
            return state.story;
        },
        stories(state) {
            return state.stories;
        }
    },
    mutations: {
        update(state, story) {
            state.story = story;
        },
        save(state, story) {
            state.stories = myLocalStorage.loadStories();
        
            if (story.id) {
                const index = state.stories.findIndex(s => s.id === story.id);
                state.stories.splice(index, 1, story);
            
            } else {
                story.id = uuidv4();
                state.story.id = story.id;
                state.stories.push(story);
            }

            myLocalStorage.saveStories(state.stories);
        },
        load(state, story) {
            state.story = story;
        }
    }
});