<template>
    <div class="page">
        <h2>Characters</h2>
        <span class="buttons">
            <button class="button" @click="addCharacter">New Character</button>
            <button class="button" @click="$router.push('/edit')">Back</button>
        </span>
        <ul class="editlist box">
            <li v-for="(character, index) in characters" :key="index">
                <input type="text" v-model="character.name" placeholder="Name" />
                <img :src="character.image" />
                <span class="field">
                    <label>Speech Box Colour</label>
                    <input type="color" v-model="character.colour" />
                </span>
                <span class="field">
                    <label>Y-Offset</label>
                    <input type="number" v-model="character.yoffset" />
                </span>
                <span class="field">
                    <label>Scale</label>
                    <input type="number" v-model="character.scale" />
                </span>
                <Canvas :id="'canvas' + index" :width="304" :height="190" :character="character" text="Hi" />
                <UploadFile v-model="character.image" :id="'uploadCharacter' + index" />
            </li>
        </ul>
    </div>
</template>

<script>
import UploadFile from "@/components/UploadFile.vue";
import Canvas from "@/pages/play/Canvas.vue";

export default {
    components: { UploadFile, Canvas },
    data() {
        return {
            characters: [],
        }
    },
    mounted() {
        this.characters = this.$store.getters.story.characters;
    },
    methods: {
        addCharacter() {
            this.characters.push({
                name: "",
                image: "",
                colour: "#69A0D7",
                yoffset: 0,
                scale: 1
            });
        }
    }
}
</script>

<style scoped>
img {
    display: inline-block;
    max-height: 400px;
}
</style>