<template>
    <div class="page">
        <h2>Scenes</h2>
        <span class="buttons">
            <button class="button" @click="addScene">New Scene</button>
            <button class="button" @click="$router.push('/edit')">Back</button>
        </span>
        <ul class="editlist box">
            <li v-for="(scene, index) in scenes" :key="index">
                <input type="text" v-model="scene.name" placeholder="Name" />
                <img :src="scene.image" />
                <UploadFile v-model="scene.image" :id="'uploadScene' + index" />
            </li>
        </ul>
    </div>
</template>

<script>
import UploadFile from "@/components/UploadFile.vue";

export default {
    components: { UploadFile },
    data() {
        return {
            scenes: [],
            checkedScenes: []
        }
    },
    mounted() {
        this.scenes = this.$store.getters.story.scenes;
    },
    methods: {
        addScene() {
            this.scenes.push({
                name: "",
                image: ""
            });
        }
    }
}
</script>

<style scoped>
img {
    display: inline-block;
    max-height: 400px;
}
</style>