<template>
    <canvas :id="id" :width="width" :height="height" @click="$emit('nextStep')" />
</template>

<script>
import $ from 'jquery';
import jcanvas from 'jcanvas';

jcanvas($, window);

export default {
    props: ["id", "width", "height", "scene", "character", "text"],
    data() {
        return {
            characterImageMeta: {
                width: 0,
                height: 0
            }
        }
    },
    mounted() {
        window.console.log("");
        window.console.log(this.id + " mounted");
        this.loadMetadata();
        this.draw();
    },
    watch: {
        scene: {
            handler() {
                window.console.log("");
                window.console.log(this.id + " scene updated");
                this.draw();
            },
            deep: true
        },
        character: {
            handler() {
                window.console.log("");
                window.console.log(this.id + " character updated");
                this.loadMetadata();
            },
            deep: true
        },
        characterImageMeta: {
            handler() {
                window.console.log("");
                window.console.log(this.id + " image meta updated");
                this.draw();
            },
            deep: true
        },
        text() {
            window.console.log("");
            window.console.log(this.id + " text updated");
            this.draw();
        }
    },
    computed: {
        xscale() {
            return this.width / 800;
        },
        yscale() {
            return this.height / 500;
        }
    },
    methods: {
        draw() {
            window.console.log(this.id + " draw ******");
            window.console.log("scene? " + (this.scene !== false));
            window.console.log("character? " + (this.character !== false));
            const canvas = this.id ? $("#" + this.id) : $("canvas");
            canvas.clearCanvas();

            this.drawBorder(canvas);
            this.drawScene(canvas);
            this.drawCharacter(canvas);
            this.drawText(canvas);
        },
        drawBorder(canvas) {
            canvas.drawRect({
                strokeStyle: '#c33',
                strokeWidth: 1,
                x: 1,
                y: 1,
                width: this.width - 2,
                height: this.height - 2,
                fromCenter: false
            });
        },
        drawScene(canvas) {
            if (this.scene && this.scene.image) {
                canvas.drawImage({
                    source: this.scene.image,
                    x: 2,
                    y: 2,
                    width: this.width - 4,
                    height: this.height - 4,
                    scale: 1,
                    fromCenter: false
                });
            }
        },
        drawCharacter(canvas) {
            if (this.character && this.character.image) {
                const y = 200 * this.yscale + (this.character.yoffset ? parseInt(this.character.yoffset) * 5 : 0);
                const characterScale = this.character.scale ? parseFloat(this.character.scale) : 1;
                window.console.log("width: " + this.characterImageMeta.width * this.xscale);
                window.console.log("height: " + this.characterImageMeta.height * this.yscale);
                canvas.drawImage({
                    source: this.character.image,
                    x: 10 * this.xscale,
                    y: y,
                    width: this.characterImageMeta.width * this.xscale * characterScale,
                    height: this.characterImageMeta.height * this.yscale * characterScale,
                    fromCenter: false
                });
                
            }
        },
        drawText(canvas) {
            const bgCol = this.character.colour ? this.character.colour : '#69A0D7';
            const bgBorderCol = '#2D4258';
            const textCol = '#ECF3FA';
            // write text
            canvas.drawRect({
                fillStyle: bgCol,
                x: 20 * this.xscale,
                y: 20 * this.yscale,
                width: 770 * this.xscale,
                height: 80 * this.yscale,
                fromCenter: false,
                cornerRadius: 4
            });
            canvas.drawRect({
                strokeStyle: bgBorderCol,
                x: 24 * this.xscale,
                y: 24 * this.yscale,
                width: 762 * this.xscale,
                height: 72 * this.yscale,
                fromCenter: false,
                cornerRadius: 2
            });
            canvas.drawText({
                name: "drawText",
                fillStyle: textCol,
                strokeWidth: 1,
                x: 34 * this.xscale, y: 34 * this.yscale,
                fontSize: (18 * this.xscale) + 'pt',
                fromCenter: false,
                text: "\"" + this.text + "\""
            });
            canvas.drawText({
                name: "drawText",
                fillStyle: textCol,
                strokeWidth: 1,
                x: 45 * this.xscale, y: 74 * this.yscale,
                fontSize: (18 * this.xscale) + 'pt',
                text: "..."
            });
        },
        loadMetadata() {
            let img = new Image();

            img.onload = () => {
                window.console.log("loaded metadata");
                this.characterImageMeta.width = img.width;
                this.characterImageMeta.height = img.height;
                this.draw();
            }

            img.src = this.character.image;
        }
    }
}
</script>