<template>
    <div class="page">
        <h2>Plan</h2>
        <span class="buttons">
            <button class="button" @click="newItem">New</button>
            <button class="button" @click="$router.push('/edit')">Back</button>
        </span>
        <ul class="editlist">
            <li v-for="(item, index) in plan" :key="index">
                <span class="number">{{ index + 1 }}</span>
                <span class="content">
                    <span v-if="item.scene !== undefined" class="field">
                        <label>Scene: </label>
                        <select v-model="item.scene">
                            <option v-for="(scene, sceneIndex) in scenes" :key="sceneIndex">{{ scene.name }}</option>
                        </select>
                    </span>
                    <button class="button small" v-else @click="changeScene(item)">Change scene</button>

                    <span v-if="item.character !== undefined" class="field">
                        <label>Character: </label>
                        <select v-model="item.character">
                            <option v-for="(character, characterIndex) in characters" :key="characterIndex">{{ character.name }}</option>
                        </select>
                    </span>
                    <button class="button small" v-else @click="changeCharacter(item)">Change character</button>

                    <span class="field">
                        <label>Message: </label>
                        <textarea v-model="item.text"></textarea>
                    </span>
                </span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    data() {
        return {
            plan: [],
            scenes: [],
            characters: []
        }
    },
    mounted() {
        this.plan = this.$store.getters.story.plan;
        this.scenes = this.$store.getters.story.scenes;
        this.characters = this.$store.getters.story.characters;
    },
    methods: {
        newItem() {
            this.plan.push({scene: undefined, character: undefined, text: ""});
        },
        changeScene(item) {
            item.scene = "";
        },
        changeCharacter(item) {
            item.character = "";
        }
    }
}
</script>