const STORIES_KEY = "stories";

export default {
    loadStories() {
        const savedStories = window.localStorage.getItem(STORIES_KEY);
        let stories = [];
        if (savedStories) {
            stories = JSON.parse(savedStories);
        }
        return stories;
    },

    saveStories(stories) {
        window.localStorage.setItem(STORIES_KEY, JSON.stringify(stories));
    }
}