<template>
    <span class="upload">
        <input :id="getId" type="file" @change="uploadFile($event)" />
        <label :for="getId" class="button">{{ label ? label : 'Upload' }}</label>
    </span>
</template>

<script>
export default {
    props: ["value", "text", "id", "label"],
    data() {
        return {
            reader: new FileReader()
        }
    },
    mounted() {
        this.reader.addEventListener("load", this.whenFileLoaded, false);
    },
    computed: {
        getId() {
            return this.id ? this.id : "file";
        }
    },
    methods: {
        uploadFile(event) {
            var file = event.target.files[0];
            if (this.text) {
                this.reader.readAsText(file);
            } else {
                this.reader.readAsDataURL(file);
            }
        },
        whenFileLoaded() {
            this.$emit("input", this.reader.result);
        }
    }
}
</script>
