import Vue from 'vue'
import VueRouter from 'vue-router';
import Home from "@/pages/Home.vue";
import Editor from "@/pages/editor/Editor.vue";
import Scenes from "@/pages/editor/Scenes.vue";
import Characters from "@/pages/editor/Characters.vue";
import Plan from "@/pages/editor/Plan.vue";
import Play from "@/pages/play/Play.vue";

const routes = [
    { path: '/', component: Home },
    { path: '/edit', component: Editor },
    { path: '/edit/scenes', component: Scenes },
    { path: '/edit/characters', component: Characters },
    { path: '/edit/plan', component: Plan },
    { path: '/play', component: Play }
]

const router = new VueRouter({
    mode: "hash",
    routes
});

Vue.use(VueRouter);

export default router;