<template>
    <div class="page">
        <h1>Story Maker</h1>
        <p>Loaded Story: {{ story.title }}</p>
        <button class="wideMenu button" @click="$router.push('/play')">Play</button>
        <button class="wideMenu button" @click="$router.push('/edit')">Edit</button>
        <hr />
        <UploadFile v-model="importedStory" :text="true" id="uploadStory" label="Import" />
        <button class="wideMenu button" @click="exportStory">Export</button>

        <h2>Saved Stories</h2>
        <button class="wideMenu button" v-for="(story, index) in stories" :key="index" @click="loadStory(story)">Load {{ story.title }}</button>
    </div>
</template>

<script>
import UploadFile from "@/components/UploadFile.vue";

export default {
    components: { UploadFile },
    dat() {
        return {
            importedStory: {}
        }
    },
    computed: {
        story() {
            return this.$store.getters.story;
        },
        stories() {
            return this.$store.getters.stories;
        }
    },
    watch: {
        importedStory(uploadedStory) {
            // this.story = JSON.parse(uloadedStory);
            this.$store.commit("update", uploadedStory);
        }
    },
    methods: {
        loadStory(story) {
            this.$store.commit("load", story);
        },
        exportStory(data, filename, type) {
            var file = new Blob([data], {type: type});
            if (window.navigator.msSaveOrOpenBlob) // IE10+
                window.navigator.msSaveOrOpenBlob(file, filename);
            else { // Others
                var a = document.createElement("a"),
                url = URL.createObjectURL(file);
                a.href = url;
                a.download = filename;
                document.body.appendChild(a);
                a.click();
                setTimeout(function() {
                    document.body.removeChild(a);
                    window.URL.revokeObjectURL(url);  
                }, 0); 
            }
        },
    }
}
</script>